@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;700;900&display=swap);
body{margin:0;font-family:'Montserrat Alternates', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#dce8ef;color:#424242}a{text-decoration:none;color:#528199}.page-container{min-height:100vh;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;display:-webkit-flex;display:flex}

.link-keeper__container{background-color:#f0f5f9;max-width:767px;width:100%;height:80vh;border-radius:30px;overflow:hidden;box-shadow:0px 10px 25px 0px rgba(0,0,0,0.09);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative}@media (max-width: 767px){.link-keeper__container{width:100%;height:100vh;padding:0;border-radius:0}}.link-keeper__content{padding:0 10px}

.btn{border-radius:40px;padding:13px 25px;outline:none;cursor:pointer;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:0;transition:0.1s}.btn:hover{opacity:0.8}.btn.add{font:400 25px 'Montserrat Alternates', sans-serif}@media (max-width: 480px){.btn.add{font:400 16px 'Montserrat Alternates', sans-serif}}.btn.orange{background-color:#ff844b;box-shadow:0 5px 15px -5px rgba(255,132,75,0.8);color:white}.btn.orange svg{-webkit-transform:rotate(90deg);transform:rotate(90deg);transition:-webkit-transform 100ms linear;transition:transform 100ms linear;transition:transform 100ms linear, -webkit-transform 100ms linear}.btn.orange-200{background-color:rgba(255,132,75,0.2);color:#ff844b}.btn.red{background-color:#e23737;box-shadow:0 5px 15px -5px rgba(226,55,55,0.8);color:white}.btn.red svg{-webkit-transform:rotate(45deg);transform:rotate(45deg);transition:-webkit-transform 100ms linear;transition:transform 100ms linear;transition:transform 100ms linear, -webkit-transform 100ms linear}.btn.blue-200{background-color:rgba(81,128,153,0.2);color:#2d3e5f}.btn.icon svg{font-size:30px;margin-left:-12px;margin-right:10px}@media (max-width: 480px){.btn.icon svg{font-size:20px}}.btn.circle{height:45px;width:45px;font-size:16px;padding:0}

.link-keeper__header{display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:space-between;justify-content:space-between;padding:20px 15px}@media (max-width: 767px){.link-keeper__header{margin-bottom:0;padding:20px 15px}}.link-keeper__header h1{font-weight:400;font-size:28px;color:#424242;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;-webkit-margin-start:0;margin-inline-start:0;-webkit-margin-end:0;margin-inline-end:0}.link-keeper__header h1 small{display:block;font-size:14px;margin-left:37px}@media (max-width: 480px){.link-keeper__header h1 small{font-size:12px;margin-left:24px}}@media (max-width: 480px){.link-keeper__header h1{font-size:18px}}

.link-keeper__list-item{background-color:white;padding:20px;margin:0 15px;border-radius:30px;box-shadow:0px 18px 20px 0px rgba(0,0,0,0.03);cursor:pointer;display:block;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}@media (max-width: 480px){.link-keeper__list-item{margin:0}}.link-keeper__list-item:hover{background-color:rgba(220,232,239,0.7);transition:0.1s}.link-keeper__list-item+.link-keeper__list-item{margin-top:20px}.link-keeper__list-item-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:15px}.link-keeper__list-item-actions{display:-webkit-flex;display:flex}.link-keeper__list-item-actions button+button{margin-left:20px}.link-keeper__list-item-actions a+button{margin-left:20px}.link-keeper__list-item-title{font-size:20px;padding-right:20px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.link-keeper__list-item-title small{display:block;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:14px;margin-top:10px}.link-keeper__list-item-link{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.link-keeper__list-item-desc{font-size:14px}

.link-keeper__list{-webkit-flex:1 1;flex:1 1;overflow:hidden}@media (max-width: 767px){.link-keeper__list{padding:0 15px}}.link-keeper__list-empty{height:100%;-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;font-size:24px;color:rgba(0,0,0,0.2)}.link-keeper__list-outside{height:100%;overflow:auto}@media (max-height: 1024px), (max-width: 767px){.link-keeper__list-outside{width:100%}}.link-keeper__list-inside{width:100%;margin:0 0px;padding-bottom:30px}

.link-keeper__form{-webkit-flex:1 1;flex:1 1;padding:0 10px}.link-keeper__form-control{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.link-keeper__form-control+.link-keeper__form-control{margin-top:40px}.link-keeper__form-label{font-size:18px;margin-bottom:10px}.link-keeper__form-input{font-size:24px;outline:none;padding:10px 0;background-color:transparent;border:0;max-width:100%;border-bottom:2px solid rgba(0,0,0,0.05)}

.link-keeper__footer{text-align:center;padding-top:20px;padding:20px 15px}

.link-keeper__notification{position:absolute;bottom:80px;right:50%;-webkit-transform:translate(50%, 0);transform:translate(50%, 0);background:#dce8ef;padding:20px 30px;font-size:14px;color:#528199;border-radius:20px;box-shadow:0px 18px 20px 0px rgba(0,0,0,0.03);opacity:0;visibility:hidden;transition:opacity 100ms linear}.link-keeper__notification.active{opacity:1;visibility:visible}.link-keeper__notification.rose{background-color:#e3e4fd}

